export default function parseCSSValue(rawCSS?: string | number): {
  value: number;
  unit: string;
} {
  const matchCSS =
    typeof rawCSS === 'string'
      ? rawCSS.match(/([0-9]{1,})([A-Za-z]{1,})/)
      : null;

  return matchCSS
    ? {
        value: Number(matchCSS[1]),
        unit: matchCSS[2],
      }
    : {
        value: Number(rawCSS) || 0,
        unit: 'px',
      };
}
