import React from 'react';

import parseCSSValue from './parseCSSValue';

type CSS3DCarouselProps = React.HTMLAttributes<HTMLElement> & {
  children: React.ReactNode;
  itemPerspective: React.CSSProperties['perspective'];
  itemHeight: React.CSSProperties['height'];
  itemWidth: React.CSSProperties['width'];
  activeIndex: number;
  rotate?: number;
};

function CSS3DCarousel({
  children,
  itemPerspective,
  itemHeight,
  itemWidth,
  activeIndex,
  rotate,
  ...props
}: CSS3DCarouselProps) {
  const { value: widthValue, unit: widthUnit } = React.useMemo(
    () => parseCSSValue(itemWidth),
    [itemWidth]
  );

  const { length } = React.useMemo(
    () =>
      React.Children.toArray(children).filter((child) =>
        React.isValidElement(child)
      ),
    [children]
  );

  const radius = React.useMemo(
    () => widthValue / 2 / Math.tan(Math.PI / length),
    [widthValue, length]
  );

  return (
    <div {...props}>
      <div
        style={{
          width: itemWidth,
          height: itemHeight,
          perspective: itemPerspective,
          position: 'relative',
        }}
      >
        <div
          style={{
            width: '100%',
            height: '100%',
            position: 'absolute',
            transformStyle: 'preserve-3d',
            transform: `rotateY(${rotate || activeIndex * (-360 / length)}deg)`,
          }}
        >
          {React.Children.map(children, (child, index) =>
            React.isValidElement(child)
              ? React.cloneElement(child, {
                  style: {
                    width: itemWidth,
                    height: itemHeight,
                    position: 'absolute',
                    transform: `rotateY(${
                      index === 0 ? 0 : index * (360 / length)
                    }deg) translateZ(${radius}${widthUnit})`,
                    ...child.props.style,
                  },
                } as { style: React.CSSProperties })
              : null
          )}
        </div>
      </div>
    </div>
  );
}

export default React.memo(CSS3DCarousel);
